<template>
  <div>
  <a-alert message="注意:只允许为三级分类设置相关参数!" type="warning" show-icon />
  </div>
  <div style="margin:20px">
    <span>选择商品分类:</span>
        <a-cascader
          :options="allCateList"
          :display-render="displayRender"
          expand-trigger="hover"
          placeholder="请选择分类"
          :fieldNames="{ label: 'cat_name', value: 'cat_id', children: 'children' }"
        />
  </div>
  <div>
    <a-tabs v-model:activeKey="activeKey" @change="tabChangeHandler" :pagination="false">
      <a-tab-pane :disabled="isNotSelected" key="many" tab="动态参数">
          <a-button :disabled="isNotSelected" type="primary" style="margin-bottom:20px" @click="showAddParamsBox('many')">添加属性</a-button>
           <a-table v-if="paramsList.length!=0" :columns="columns" :data-source="paramsList" rowKey="attr_id" bordered :pagination="false">
            <template #handler="{record}">
              <a-button type="primary" size="small" @click="showAddParamsBox('many',record)">编辑</a-button>
              <a-button type="primary" danger size="small" @click="deleteManyParams(record.cat_id,record.attr_id)">删除</a-button>
            </template>
            <template #expandedRowRender="{record}">
              <a-tag color="pink" v-for="(item,index) in record.attr_vals.split(',')" :key="index">{{item}}</a-tag>
            </template>
           </a-table>
      </a-tab-pane>
      <a-tab-pane :disabled="isNotSelected" key="only" tab="静态属性" force-render>
          <a-button :disabled="isNotSelected" type="primary" style="margin-bottom:20px" @click="showAddParamsBox('only')">添加属性</a-button>
           <a-table v-if="paramsList.length!=0" :columns="columns" :data-source="paramsList" rowKey="attr_id" bordered :pagination="false">
              <template #handler="{record}">
              <a-button type="primary" size="small" @click="showAddParamsBox('only',record)">编辑</a-button>
              <a-button type="primary" danger size="small" @click="deleteManyParams(record.cat_id,record.attr_id)">删除</a-button>
            </template>
           </a-table>
      </a-tab-pane>
      </a-tabs>
      <!-- 添加参数的表单 -->
        <a-modal v-model:visible="showAddParamsVisible" title="添加参数" @ok="addParamsOkHandler">
          <a-form :model="paramsFormState">
            <a-form-item label="属性名称">
              <a-input v-model:value="paramsFormState.attr_name" />
            </a-form-item>
             <a-form-item label="属性值">
              <a-input v-model:value="paramsFormState.attr_vals" />
            </a-form-item>
            <a-alert message="注意:每一个属性之间请用英文逗号分割 ! ! !" type="warning" show-icon />
          </a-form>
    </a-modal>
  </div>
</template>

<script>
import { getCurrentInstance, onMounted, reactive, ref } from 'vue'
import { message } from 'ant-design-vue'
export default {
  setup () {
    // 获取VUE实例
    const { proxy } = getCurrentInstance()
    // 级联选择框的数据源
    const allCateList = ref([])
    // 请求参数列表所需参数
    const queryParams = {
      sel: 'many'
    }
    // 查询所有分类的请求函数
    const queryAllCateList = async () => {
      const { meta, data } = await proxy.$http.get('categories')
      if (meta.status != 200) return message.error(meta.msg)
      allCateList.value = data
    }
    onMounted(() => {
      queryAllCateList()
    })
    // 请求参数列表所需要的当前选中的分类ID
    // let currentCateID = 0
    const currentCateID = ref()
    // 判断是否未选择状态
    const isNotSelected = ref(true)
    // 渲染联级选择框 每次变化的时候触发
    const displayRender = ({ labels, selectedOptions }) => {
      if (selectedOptions.length != 0) {
        // 如果执行了if 代表选择分类  false
        isNotSelected.value = false
        currentCateID.value = selectedOptions[selectedOptions.length - 1].cat_id
        queryParamsList()
      } else {
        // 如果执行了else 代表未选择任何分类  状态改为true
        isNotSelected.value = true
        paramsList.value = []
      }
      return labels[labels.length - 1]
    }

    // 当前默认选中的激活的key标签
    const activeKey = ref('many')

    // 表格数据源
    const paramsList = ref([])
    // 请求参数列表的请求函数
    const queryParamsList = async () => {
      const { meta, data } = await proxy.$http.get(`categories/${currentCateID.value}/attributes`, {
        params: queryParams
      })
      if (meta.status != 200) return message.error(meta.msg)
      paramsList.value = data
    }
    // 当tab栏切换事触发的处理函数
    const tabChangeHandler = (newKey) => {
      paramsList.value = ''
      queryParams.sel = newKey
      queryParamsList()
    }
    // 表格渲染规则
    const columns = [
      {
        title: '属性名字',
        dataIndex: 'attr_name'
      },
      {
        title: '属性值',
        dataIndex: 'attr_vals'
      },
      {
        title: '操作',
        width: '20%',
        slots: { customRender: 'handler' }
      }
    ]
    // 添加参数表单的数据绑定
    const paramsFormState = reactive({
      attr_name: '',
      attr_vals: ''
    })
    // 控制对话框显示的变量
    const showAddParamsVisible = ref(false)
    // 保存当前激活的标签状态栏
    let isManyOrOnly = 'many'
    // 点击显示添加属性对话框盒子弹窗
    // 当前修改属性的ID
    let currentParamsID = ''
    const showAddParamsBox = (manyOrOnly, record) => {
      if (record) {
        currentParamsID = record.attr_id
        paramsFormState.attr_name = record.attr_name
        paramsFormState.attr_vals = record.attr_vals
      }
      isManyOrOnly = manyOrOnly
      showAddParamsVisible.value = true
    }
    // 点了对话框确定按钮的操作
    const addParamsOkHandler = async () => {
      showAddParamsVisible.value = false
      // 判断是添加还是编辑
      // 编辑操作
      if (currentParamsID) {
        const { meta } = await proxy.$http.put(`categories/${currentCateID.value}/attributes/${currentParamsID}`, {
          attr_name: paramsFormState.attr_name,
          attr_sel: isManyOrOnly,
          attr_vals: paramsFormState.attr_vals
        })
        if (meta.status != 200) return message.error(meta.msg)
        message.success(meta.msg)
        queryParamsList()
        // 添加操作
      } else {
        const { meta } = await proxy.$http.post(`categories/${currentCateID.value}/attributes`, {
          attr_name: paramsFormState.attr_name,
          attr_sel: isManyOrOnly,
          attr_vals: paramsFormState.attr_vals
        })
        if (meta.status != 201) return message.error(meta.msg)
        message.success(meta.msg)
        queryParamsList()
      }
      // 关闭对话框,清空表单
      paramsFormState.attr_name = ''
      paramsFormState.attr_vals = ''
    }
    // 删除商品属性
    const deleteManyParams = async (Id, attrId) => {
      const { meta } = await proxy.$http.delete(`categories/${Id}/attributes/${attrId}`)
      if (meta.status != 200) return message.error(meta.msg)
      message.success(meta.msg)
      queryParamsList()
    }
    return {
      showAddParamsVisible,
      paramsFormState,
      deleteManyParams,
      showAddParamsBox,
      addParamsOkHandler,
      displayRender,
      allCateList,
      activeKey,
      columns,
      isNotSelected,
      paramsList,
      tabChangeHandler,
      queryAllCateList
    }
  }
}
</script>

<style scoped>

</style>
